.wp-block-block-course-todo-list{
	ul {
		list-style: none;
		padding: 0;
		margin: 0;

		li {
			margin-bottom:  15px;
			label {
				font-size: 16px;
			}
			&.todo-completed{
			 opacity: 0.5;
			 text-decoration: line-through;
			}
		}
	}
	.addtodo-form {
		display: flex;
		margin-top: 30px;
		input {
			height: 36px;
		}
		button {
			margin-left: 20px;
		}
	}
}